.nav {
  &__list {
    display: flex;
    flex-direction: column;
    gap: 32px;
    margin: 0;
    padding: 0;
    list-style: none;

  }

  &__link {
    position: relative;
    color: $nav-text-color;
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;

    &::after {
      content: "";
      display: block;
      position: absolute;
      bottom: -8px;
      height: 1px;
      width: 100%;

      background-color: $nav-text-color;
      transition: transform 0.3s;
      transform: scale(0);
      transform-origin: left;

    }

    &:hover::after {
      transform: scale(1);
    }

  }
}
